import { motion, useMotionValue, useTransform } from "framer-motion"
import { useCallback, useEffect, useLayoutEffect, useRef, useState } from "react"
import Card from "./Card"

export interface Meme {
  id: string,
  name: string,
  url: string,
  width: number,
  height: number,
  box_count: number
}

const ImagesHolder: React.FC = () => {

  const [images, setImages] = useState<Meme[]>([])
  const [image, setImage] = useState<Meme | null>(null)
  const [init, setInit] = useState<boolean>(false)
  const [audios] = useState({
    halal: new Audio('audios/halal.mp3'),
    haram: new Audio('audios/haram.mp3')
  }) 
  const x = useMotionValue(0);
  const rotate = useTransform(x, latest => latest * 0.05)
  const opacityHalal = useTransform(x, [0, 100], [0, 1])
  const opacityHaram = useTransform(x, [0, -100], [0, 1])
  const volumeHalal = useTransform(x, [0, 200], [0, 1])
  const volumeHaram = useTransform(x, [0, -200], [0, 1])
  const xInput = [-100, 0, 100];
  const background = useTransform(x, xInput, [
    "#dc3545",
    "#F7F7F9",
    "#28a745"
  ]);

  useEffect(() => {
    const getImages = async () => {
      const response = await fetch('https://api.imgflip.com/get_memes')
      const { data : { memes } }: { data: { memes: Meme[] } } = await response.json()
      setImages(memes)
    }
    getImages()
  }, [])

  const nextImage = useCallback(() => {
    if (!images.length) return
    const myImages = [...images]
    setImage(myImages.splice(
      Math.floor(Math.random() * myImages.length),
      1
    )[0])
  }, [images])
  
  useEffect(() => {
    nextImage()
  }, [nextImage])

  useLayoutEffect(() => {
    if (init) return
    const { haram, halal } = audios
    document.body.appendChild(haram)
    document.body.appendChild(halal)
    setInit(true)
  }, [audios, init])

  x.onChange((val) => {
    const { halal, haram } = audios
    halal.volume = volumeHalal.get();
    haram.volume = volumeHaram.get();
    if (val < 0 ) {
      if (!haram.paused) return
      halal.pause()
      halal.currentTime = 0
      haram.currentTime = 0
      haram.play()
    }
    else if (val > 0) {
      if (!halal.paused) return
      haram.pause()
      haram.currentTime = 0
      halal.currentTime = 0
      halal.play()
    }
    else {
      haram.pause()
      halal.pause()
      haram.currentTime = 0
      halal.currentTime = 0
    }
  })



  const dragEnd = () => {
    if (x.get() >= 100) {
      nextImage()
    }
    else if (x.get() <= 100) {
      nextImage()
    }
  }

  return (
    <motion.div className="flex items-center justify-center overflow-hidden relative select-none" style={{ background }}>
      <div className="flex absolute inset-0 items-center justify-between font-bold text-4xl px-4">
        <span style={{ color: '#28a745' }}>HARAM</span>
        <span style={{ color: '#dc3545' }}>HALAL</span>
      </div>
      <motion.div className="absolute left-0 top-0 w-1/3" style={{ opacity: opacityHalal }}>
        <img
          src="https://w7.pngwing.com/pngs/678/427/png-transparent-emoji-emoticon-smiley-computer-icons-cool-miscellaneous-thumb-signal-sticker.png"
          alt="halal"
        />
      </motion.div>
      {
        image && (
          <div className="h-screen aspect-[3/6] mx-auto py-4 relative z-20">
            <motion.div
              className="bg-rose-800 p-4 rounded h-full flex flex-col shadow-lg shadow-black/60"
              drag="x"
              style={{ x, rotate, transformOrigin: '50% 80%' }}
              dragSnapToOrigin
              dragConstraints={{ left: 0, right: 0 }}
              dragElastic={0.2}
              whileDrag={{ scale: 1.01 }}
              onDragEnd={dragEnd}
            >
              <div className="grow bg-white rounded-lg overflow-hidden flex items-center justify-center">
                <img className=" pointer-events-none" src={image.url} alt={image.name} />
              </div>
            </motion.div>
          </div>
        )
      }
      <motion.div className="absolute right-0 top-0 w-1/4" style={{ opacity: opacityHaram }}>
        <img
          src="https://e1.pngegg.com/pngimages/708/305/png-clipart-emos-angry-smiley-3d-icon.png"
          alt="halal"
        />
      </motion.div>
    </motion.div>
  )

}

export default ImagesHolder